import {
  Anchor,
  Button,
  Flex,
  Line,
  PagePaddingX,
  Text,
  useClientMountedState,
  useMediaQuery,
  useScroll,
} from '@fx/app-front-end-components';
import type { SEOProps } from '@fx/lib-front-end-app-utilities';
import { Page } from '@fx/lib-front-end-app-utilities';
import classNames from 'classnames';
import { useMemo, useRef } from 'react';
import useMouse from '@react-hook/mouse-position';
import {
  Swiper,
  SwiperSlide,
  SwiperSlideContents,
} from 'components/common/Swiper';
import { Pagination, Autoplay, EffectFade } from 'swiper/modules';
import { ExternalLinkIcon } from '@radix-ui/react-icons';
import { Header } from 'components/common/Header/Header';
import { SEO_CONFIG } from 'config/seo';
import styles from './Home.module.scss';

const SEO_PROPS: SEOProps = {
  title: 'Home',
  description: SEO_CONFIG.DESCRIPTION.DEFAULT,
  url: SEO_CONFIG.URL.CANONICAL,
  sharingImageURL: SEO_CONFIG.SHARING_IMAGE_URL.DEFAULT,
};

const HUE_ROTATE_MOUSE_INCLUDE_X = false;
const HUE_ROTATE_MOUSE_INCLUDE_Y = false;
const HUE_ROTATE_DIRECTION_REVERSE = false;
const HUE_ROTATE_CYCLE_PERCENTAGE = 1;
const HUE_ROTATE_STARTING_DEG = 0;

type MediaSources = (
  | {
      type: 'img';
      path: string;
    }
  | {
      type: 'video';
      path: string;
      videoType: string;
    }
)[];

const HERO_MEDIA_SOURCES_DESKTOP: MediaSources = [
  // {
  //   type: 'img',
  //   path: '/media/desktop/images/hero/affector-genosys-1.png',
  // },
  {
    type: 'video',
    path: '/media/desktop/videos/hero/1.mp4',
    videoType: 'video/mp4',
  },
  // { type: 'img', path: '/media/desktop/images/hero/affector-genosys-2.png' },
  // { type: 'img', path: '/media/desktop/images/hero/affector-genosys-3.png' },
];

const HERO_MEDIA_SOURCES_MOBILE: MediaSources = [
  // { type: 'img', path: '/media/mobile/images/hero/affector-genosys-1.png' },
  {
    type: 'video',
    path: '/media/mobile/videos/hero/1.mp4',
    videoType: 'video/mp4',
  },
  // { type: 'img', path: '/media/mobile/images/hero/affector-genosys-2.jpg' },
  // { type: 'img', path: '/media/mobile/images/hero/affector-genosys-3.png' },
];

export const Home = () => {
  const { isMountedClient } = useClientMountedState();

  const isSmallDesktop =
    useMediaQuery(`(max-width: 1480px)`) || !isMountedClient;
  const isMobile = useMediaQuery(`(max-width: 840px)`) || !isMountedClient;

  const scrollRef = useRef<HTMLDivElement>(null);
  const heroRef = useRef(null);
  const heroContentRef = useRef(null);

  const { scrollTop } = useScroll({
    scrollContainerElementRef: scrollRef,
  });

  const mouse = useMouse(isMountedClient ? document.body : null, {
    enterDelay: 100,
    leaveDelay: 100,
  });

  const heroStyle = useMemo(() => {
    const hueRotateScroll = isMountedClient
      ? (360 * (scrollTop / document.body.clientHeight)) % 360
      : 0;
    const hueRotateMouse = isMountedClient
      ? ((360 *
          ((HUE_ROTATE_MOUSE_INCLUDE_X ? mouse?.x || 0 : 0) +
            (HUE_ROTATE_MOUSE_INCLUDE_Y ? mouse?.y || 0 : 0))) /
          ((HUE_ROTATE_MOUSE_INCLUDE_X ? document.body.clientWidth : 0) +
            (HUE_ROTATE_MOUSE_INCLUDE_Y ? document.body.clientHeight : 0))) %
        360
      : 0;
    const hueRotate =
      HUE_ROTATE_STARTING_DEG +
      ((HUE_ROTATE_MOUSE_INCLUDE_X || HUE_ROTATE_MOUSE_INCLUDE_Y
        ? hueRotateMouse
        : 0) +
        hueRotateScroll) *
        HUE_ROTATE_CYCLE_PERCENTAGE;

    return {
      filter: `hue-rotate(${
        HUE_ROTATE_DIRECTION_REVERSE ? '-' : ''
      }${hueRotate}deg)`,
    } as React.CSSProperties;
  }, [scrollTop, mouse, isMountedClient]);

  const heroContent = useMemo(() => {
    return (
      <div
        className={classNames(styles.heroContent, {
          [styles.isAbsolute]: !isMobile,
        })}
        ref={heroRef}
      >
        <PagePaddingX className={styles.heroContentPadding}>
          <Flex className={styles.heroContentBox} ref={heroContentRef}>
            <Flex className={styles.heroTitle}>
              <img
                alt="affector.io logo"
                className={styles.logo}
                src="/media/icons/apple-touch-icon.png"
              />
              <Text
                fontSize="xl"
                // italic
                useHeaderFont
              >
                FFECTOR
              </Text>
            </Flex>
            {/* <Flex className={styles.heroTitle}>
              <Text
                fontSize="xl"
                // italic
                useHeaderFont
              >
                AFFECTOR
              </Text>
            </Flex> */}

            <Flex className={styles.words} style={heroStyle}>
              <Text fontSize="xl" useHeaderFont>
                Unique.
              </Text>
              <Text fontSize="xl" useHeaderFont>
                Programmable.
              </Text>
              <Text fontSize="xl" useHeaderFont>
                Live.
              </Text>
            </Flex>
            <Text className={styles.heroDescription} fontSize="xs">
              {/* <Text fontSize="lg" italic useHeaderFont>
                AFFECTOR
              </Text>{' '} */}
              Affector revolutionises festival stages. Our exclusive
              visualisation engine creates mind-bending, audio synchronized
              visual experiences.
            </Text>
            <Text className={styles.heroDescription} fontSize="xs">
              We pioneered the breath-taking visual projections for the iconic{' '}
              <Anchor
                href="https://mixmag.net/feature/affector-genosys-block9-return-2024-honey-dijon-photek-ok-williams"
                opensInNewTab
                useSpanInline
              >
                Genosys Stage
              </Anchor>{' '}
              at Glastonbury Festival.
            </Text>
          </Flex>
        </PagePaddingX>
      </div>
    );
  }, [heroStyle, isMobile]);

  const mixmagLink = useMemo(
    () => (
      <Anchor
        href="https://mixmag.net/feature/affector-genosys-block9-return-2024-honey-dijon-photek-ok-williams"
        opensInNewTab
      >
        <Button className={styles.anchorButton} isWrapper>
          <Text useHeaderFont>MIXMAG</Text>
          <ExternalLinkIcon />
        </Button>
      </Anchor>
    ),
    []
  );

  const djmagLink = useMemo(
    () => (
      <Anchor
        href="https://djmag.com/features/glastonburys-block9-photos-raising-bar-electronic-music-stage-production"
        opensInNewTab
      >
        <Button className={styles.anchorButton} isWrapper>
          <Text useHeaderFont>DJ MAG</Text>
          <ExternalLinkIcon />
        </Button>
      </Anchor>
    ),
    []
  );

  return (
    <Page
      className={classNames(styles.homePage, {
        [styles.visible]: isMountedClient,
      })}
      seoProps={SEO_PROPS}
    >
      <Header heroContentRef={heroContentRef} scrollRef={scrollRef} />
      <Flex className={styles.pageContentWrapper}>
        <Flex className={styles.pageContent} ref={scrollRef}>
          <div
            className={classNames(styles.hero, {
              [styles.visible]: isMountedClient,
            })}
          >
            <Flex className={styles.heroCarouselWrapper} style={heroStyle}>
              <Swiper
                autoplay={{ delay: 3500 }}
                className={styles.heroCarousel}
                effect="fade"
                enabled
                loop
                modules={[Autoplay, Pagination, EffectFade]}
                navigation={false}
                pagination={false}
                slidesPerView={1}
                spaceBetween={0}
                speed={1000}
              >
                {(isMobile
                  ? HERO_MEDIA_SOURCES_MOBILE
                  : HERO_MEDIA_SOURCES_DESKTOP
                ).map((item) => (
                  <SwiperSlide key={item.path}>
                    <SwiperSlideContents className={styles.carouselItem}>
                      {item.type === 'img' ? (
                        <img
                          alt="affector hero"
                          className={styles.heroImage}
                          src={item.path}
                        />
                      ) : (isMountedClient ? (
                        <video autoPlay loop muted playsInline preload="none">
                          <source src={item.path} type={item.videoType} />
                        </video>
                      ) : null)}
                    </SwiperSlideContents>
                  </SwiperSlide>
                ))}
              </Swiper>
            </Flex>

            <div className={styles.heroDarkPane} />
            {isMobile ? null : heroContent}
            <div className={styles.heroGradientAbsolute}>
              <div className={styles.gradient} />
            </div>
            {!isMobile && (
              <div
                className={classNames(styles.heroGradientAbsolute, styles.left)}
              >
                <div className={styles.gradient} />
              </div>
            )}
          </div>
          <PagePaddingX>
            <Flex className={styles.pageBodyContent}>
              {isMobile ? heroContent : null}
              {isMobile ? (
                <Flex className={styles.linksWrapperMobile} isColumn>
                  {mixmagLink}
                  <Line orientation="horizontal" />
                  {djmagLink}
                </Flex>
              ) : (
                <Flex
                  className={classNames(styles.linksWrapper, styles.carousel)}
                >
                  <Swiper
                    autoplay={{ delay: 3500 }}
                    className={styles.carousel}
                    effect="slide"
                    enabled
                    loop
                    // modules={[Autoplay, Pagination, EffectFade]}
                    slidesPerView={isMobile ? 2 : 4}
                    spaceBetween={0}
                    speed={1000}
                  >
                    <SwiperSlide>
                      <SwiperSlideContents className={styles.carouselItem}>
                        {mixmagLink}
                      </SwiperSlideContents>
                    </SwiperSlide>
                    {!isMobile && (
                      <SwiperSlide>
                        <SwiperSlideContents className={styles.carouselItem}>
                          <Text className={styles.quote}>
                            {isSmallDesktop
                              ? `"a mind-boggling
                          experience"`
                              : `"transforms the structure into a mind-boggling
                          experience"`}
                          </Text>
                        </SwiperSlideContents>
                      </SwiperSlide>
                    )}
                    {!isMobile && (
                      <SwiperSlide>
                        <SwiperSlideContents className={styles.carouselItem}>
                          <Text className={styles.quote}>
                            {isSmallDesktop
                              ? `"like you're tripping on acid"`
                              : `"makes you feel like you're tripping on acid, even if
                          you haven't taken anything"`}
                          </Text>
                        </SwiperSlideContents>
                      </SwiperSlide>
                    )}
                    <SwiperSlide>
                      <SwiperSlideContents className={styles.carouselItem}>
                        {djmagLink}
                      </SwiperSlideContents>
                    </SwiperSlide>
                    <SwiperSlide>
                      <SwiperSlideContents>
                        <Text useHeaderFont>{null}</Text>
                      </SwiperSlideContents>
                    </SwiperSlide>
                  </Swiper>
                </Flex>
              )}
              {/* <Flex className={styles.bodyContent}>
                <Flex className={styles.textBlock}>
                  <Text fontSize="sm">
                    Lorem Ipsum is simply dummy text of the printing and
                    typesetting industry. Lorem Ipsum has been the standard
                    dummy text ever since the 1500s, when an unknown printer
                    took a galley of type and scrambled it to make a type
                    specimen book. It has survived not only five centuries, but
                    also the leap into electronic typesetting, remaining
                    essentially unchanged. It was popularised in the 1960s with
                    the release of Letraset sheets containing Lorem Ipsum
                    passages, and more recently with desktop publishing software
                    like Aldus PageMaker including versions of Lorem Ipsum.
                  </Text>
                </Flex>
                <Flex className={styles.imagePairBlock}>
                  <Flex className={styles.imageBlock} style={{width: '80.4%'}}>
                    <img src='/media/showcase/demo.gif' />
                  </Flex>
                  <Flex className={styles.imageBlock} style={{width: '80.4%'}}>
                    <img src='/media/showcase/demo.gif' />
                  </Flex>
                  <Flex className={styles.imageBlock}>
                    <img src='/media/showcase/affector-ui-window.png' />
                  </Flex>
                </Flex>
              </Flex> */}
            </Flex>
          </PagePaddingX>
        </Flex>
      </Flex>
    </Page>
  );
};
